<template>
  <div class="back">
    <v-app-bar app elevation="2" color="#333744" dark>
      <v-app-bar-nav-icon @click="goBack()">
        <v-icon>mdi-arrow-left</v-icon>
      </v-app-bar-nav-icon>
      <v-toolbar-title>
        <span class="pr-8">{{ title }}</span>
      </v-toolbar-title>
      <v-spacer></v-spacer>
    </v-app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <swiper
          :options="swiperOption"
          class="swiper"
          v-if="swiperSlides.length > 0"
        >
          <swiper-slide v-for="item in swiperSlides" :key="item.id"
            ><v-img :src="item.imageurl"></v-img
          ></swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
        <v-card class="mx-auto" min-height="400">
          <v-list>
            <v-list-item v-for="item in newslist" :key="item.id" two-line>
              <v-list-item-content
                @click="handleToDetail(item.id)"
                style="cursor:pointer"
              >
                <v-list-item-title
                  v-text="item.title"
                  class="new"
                ></v-list-item-title>
                <v-list-item-subtitle v-text="item.time"></v-list-item-subtitle>
              </v-list-item-content>

              <v-list-item-action>
                <v-btn icon>
                  <v-icon color="grey lighten-1">mdi-information</v-icon>
                </v-btn>
              </v-list-item-action>
            </v-list-item>
          </v-list>
          <!-- <div class="text-center">
            <v-pagination
              v-model="page"
              :length="15"
              :total-visible="7"
            ></v-pagination>
          </div> -->
        </v-card>
      </v-container>
    </v-content>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppFooter from '@/components/App/AppFooter'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'

export default {
  name: 'newsList',
  components: {
    AppFooter,
    swiper,
    swiperSlide,
  },
  data() {
    return {
      title: '新闻中心',
      swiperOption: {
        pageView: 5,
        loop: true,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: false,
        },
        speed: 1000,
        pagination: {
          el: '.swiper-pagination',
          type: 'bullets',
        },
      },
      newslist: [],
      swiperSlides: [],
    }
  },
  methods: {
    goBack() {
      this.$router.push('/home')
    },
    async getNewsList() {
      let res = await this.$http.get('/api/home/news')
      this.newslist = res.data.data.list
    },
    async getSwiperList() {
      let res = await this.$http.get('/api/home/getImage')
      if (res.data.code != 200) return
      this.swiperSlides = res.data.data
    },
    handleToDetail(id) {
      this.$router.push('/newsList/detail/' + id)
    },
  },
  mounted() {
    this.getNewsList()
    this.getSwiperList()
  },
}
</script>

<style scoped>
.new:hover {
  color: red;
}
</style>
